body {
  font-family: "Source Sans Pro";
  font-weight: 300;
  font-size: 18px;
  line-height: 1.65;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: inherit;
  text-decoration: inherit;
  transition: color .2s ease-out;
  &.selected, &:hover {
    color: reduce(100); } }

a, b, strong, th {
  font-weight: 400; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 300; }

main {
  flex-grow: 1; }

article a {
  color: #68f; }

header a, footer a {
  color: inherit;
  font-weight: 300; }

hr {
  border: 1px solid reduce(16);
  margin: 2em 0; }

header span, header time {
  color: #949494; }

blockquote {
  border-left: 5px solid #68f;
  padding-left: 1.5em;
  margin: 0; }

body, aside, main {
  flex-direction: column; }

img {
  max-width: 100%;
  height: auto;
  border-radius: 2px; }

figure {
  margin: 1em 0; }

.twitter-tweet {
  display: flex;
  justify-content: center;
  transform: scale(0.9) /* Adjust the scale as needed */;
  transform-origin: center /* Center the transformation origin */;
  margin: 0 auto /* Center the tweet horizontally */;
  width: fit-content /* Ensure the container adapts to the new size */; }
